// modules/movieDetailsService.ts
import axios from "axios";
import { bearerToken } from "./ApiLinks"; // Token de autenticao para a API do The Movie Database (TMDb)

// Interface para representar o formato de um gnero de filme
export interface Genre {
  id: number; // ID do gnero
  name: string; // Nome do gnero
}

// Interface para representar o formato de uma companhia de produo
export interface ProductionCompany {
  id: number; // ID da companhia
  logo_path: string | null; // Caminho para o logo (pode ser nulo)
  name: string; // Nome da companhia
  origin_country: string; // Pas de origem
}

// Interface para representar os detalhes completos de um filme
export interface MovieDetailsType {
  title: string; // Ttulo do filme
  poster_path: string; // Caminho para o poster do filme
  backdrop_path: string; // Caminho para o fundo do filme
  overview: string; // Resumo do filme
  release_date: string; // Data de lanamento
  vote_average: number; // Nota mdia
  tagline: string; // Tagline do filme
  genres: Genre[]; // Lista de gneros
  runtime: number; // Durao do filme (em minutos)
  production_companies: ProductionCompany[]; // Lista de companhias de produo
  budget: number; // Oramento do filme
  revenue: number; // Receita do filme
  vote_count: number; // Nmero de votos recebidos
}

/**
 * Funo responsvel por buscar detalhes de um filme pelo ID.
 * @param id - O ID do filme a ser buscado.
 * @returns Os detalhes do filme ou null em caso de erro.
 */
export async function getMovieDetails(id: string): Promise<MovieDetailsType | null> {
  try {
    // Faz uma requisio GET  API do TMDb usando o ID fornecido
    const response = await axios.get(
      `https://api.themoviedb.org/3/movie/${id}`, // URL da API com o ID do filme
      {
        headers: {
          Authorization: `Bearer ${bearerToken}`, // Adiciona o token de autenticao no cabealho
          accept: "application/json", // Define que o cliente aceita respostas em JSON
        },
      }
    );
    return response.data; // Retorna os dados do filme obtidos da API
  } catch (error) {
    // Exibe uma mensagem de erro no console caso a requisio falhe
    console.error("Error fetching movie details:", error);
    return null; // Retorna null em caso de erro
  }
}
