import React from "react";
import Display from "../components/Display"; // Importa o componente Display, responsvel por exibir mdias.
import { nowPlayingMovies } from "../modules/ApiLinks"; // Importa o endpoint da API para filmes em exibio atualmente.

const NowPlaying = () => {
  return (
    <div>
      {/* Componente Display que exibe os filmes em exibio */}
      <Display
        apiEndPoint={nowPlayingMovies} // URL da API para obter os filmes em exibio.
        itemHeading="Now Playing Movies" // Ttulo exibido no componente.
        showButtons={true} // Indica se os botes (ex.: prximo/anterior) devem ser exibidos.
        moviesOn={true} // Define que a mdia exibida ser de filmes.
        tvShowOn={false} // Define que sries de TV no sero exibidas.
        numberOfMedia={10} // Nmero mximo de mdias a serem exibidas.
      />
    </div>
  );
};

export default NowPlaying;
