import React, { useState } from "react";
import { useNavigate } from "react-router-dom"; // Hook para redirecionamento
import "../styles/CoverPageStyle.css"; // Importa o CSS para estilizao

// Interface que define as propriedades aceitas pelo componente CoverPage
interface CoverProps {
  title: string; // Ttulo principal da pgina
  description: string; // Descrio secundria
  catchyPhrase: string; // Frase de destaque
  headerImage: string; // Caminho para a imagem de cabealho
  showHeaderImage: boolean; // Indica se a imagem deve ser exibida
  showSearch: boolean; // Indica se a barra de busca deve ser exibida
}

// Componente funcional CoverPage que exibe a capa principal do site
const CoverPage: React.FC<CoverProps> = ({
  title,
  description,
  catchyPhrase,
  headerImage,
  showHeaderImage,
  showSearch,
}) => {
  // Estado local para armazenar o texto da busca
  const [searchText, setSearchText] = useState("");

  // Hook para redirecionamento de pginas
  const navigate = useNavigate();

  // Funo que lida com a busca ao clicar no boto "Search"
  const handleSearch = () => {
    if (searchText.trim() !== "") {
      // Redireciona para a pgina de resultados de busca com o texto da busca na query string
      navigate(`/search?query=${encodeURIComponent(searchText)}`);
    }
  };

  return (
    <div className={`cover ${showHeaderImage ? "has-art" : ""}`}>
      {/* Texto principal da capa */}
      <div className="coverText">
        <span className="pill">Curadoria cinematogrfica</span>
        <h1>{title}</h1> {/* Exibe o ttulo */}
        <p>{description}</p> {/* Exibe a descrio */}
        <em>{catchyPhrase}</em> {/* Exibe a frase de destaque */}

        {/* Exibe a barra de busca se showSearch for true */}
        {showSearch && (
          <div className="searchBar">
            <input
              type="search"
              placeholder="Procura filmes ou sries..."
              value={searchText}
              onChange={(e) => setSearchText(e.target.value)}
            />
            <button onClick={handleSearch}>Pesquisar</button>
          </div>
        )}
      </div>

      {/* Exibe a imagem de cabealho se showHeaderImage for true */}
      {showHeaderImage && (
        <div className="coverImage">
          <div className="coverGlow" />
          <img src={headerImage} alt="Destaque" />
        </div>
      )}
    </div>
  );
};

export default CoverPage;
