// pages/SearchResults.tsx
import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Display from "../components/Display"; // Importa o componente para exibir resultados
// Importa a funo de busca para obter os resultados da pesquisa
import { fetchSearchResults } from "../modules/Search";

const SearchResults: React.FC = () => {
  // Obtm a localizao atual (query string da URL)
  const location = useLocation();

  // Extrai o parmetro "query" da query string
  const query = new URLSearchParams(location.search).get("query") || "";

  // Estado para armazenar os resultados da pesquisa
  const [results, setResults] = useState<any[]>([]);

  // Efeito para buscar os resultados da pesquisa quando a query mudar
  useEffect(() => {
    const getResults = async () => {
      // Chama a funo para obter os dados com base na query
      const data = await fetchSearchResults(query);
      setResults(data); // Atualiza o estado com os resultados obtidos
    };
    getResults(); // Executa a busca
  }, [query]); // Dependncia: executa novamente sempre que "query" mudar

  return (
    <div>
      {/* Componente Display para exibir os resultados da pesquisa */}
      <Display
        apiEndPoint="" // No utiliza endpoint fixo, pois usa dados personalizados
        numberOfMedia={results.length} // Define o nmero de mdias a serem exibidas
        showButtons={false} // No exibe botes de navegao
        tvShowOn={false} // Define que sries de TV no sero exibidas
        moviesOn={true} // Define que a mdia exibida ser de filmes
        itemHeading={`Resultados para "${query}"`} // Cabealho do componente
        customMedia={results} // Passa os resultados personalizados para exibio
      />
    </div>
  );
};

export default SearchResults;
