import React, { useContext, useState } from "react";
import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  Menu,
  MenuItem,
  IconButton,
} from "@mui/material";
import { AccountCircle } from "@mui/icons-material";
import { NavLink, useNavigate } from "react-router-dom";
import { AuthContext } from "../context/AuthContext";
import "../styles/HeaderStyle.css";

// Lista de itens para as tabs de navegao
const tabItems = [
  { name: "Home", link: "/" }, // Pgina inicial
  { name: "Now playing", link: "/now_playing" }, // Filmes em exibio
  { name: "Popular", link: "/popular" }, // Filmes populares
];

// Componente funcional Header
const Header: React.FC = () => {
  const { token, logout } = useContext(AuthContext); // Obtm o token e a funo de logout do AuthContext
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null); // Estado para controlar o menu dropdown
  const navigate = useNavigate(); // Hook para redirecionamento de pginas

  // Funo para abrir o menu ao clicar no cone do utilizador
  const handleMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  // Funo para fechar o menu dropdown
  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  // Funo para realizar o logout
  const handleLogout = () => {
    logout(); // Chama a funo de logout do contexto
    handleMenuClose(); // Fecha o menu
    navigate('/'); // Redireciona para a pgina inicial
  };

  return (
    <div className="navBarWrapper">
      {/* Barra de navegao principal */}
      <AppBar
        position="fixed"
        elevation={0}
        className="navBar"
        sx={{
          background: "rgba(8, 11, 24, 0.88)",
          backdropFilter: "blur(14px)",
          borderBottom: "1px solid var(--border)",
          boxShadow: "0 10px 40px rgba(0,0,0,0.35)",
        }}
      >
        <Toolbar
          className="navToolbar"
          sx={{ width: "min(1200px, 94vw)", mx: "auto", py: 1 }}
        >
          {/* Logo que redireciona para a pgina inicial */}
          <Typography
            className="logo"
            onClick={() => navigate("/")}
            style={{ cursor: "pointer" }}
          >
            Movies
          </Typography>

          {/* Links de navegao */}
          <nav className="navLinks">
            {tabItems.map((tab) => (
              <NavLink
                to={tab.link}
                key={tab.link}
                end={tab.link === "/"}
                className={({ isActive }) =>
                  `nav-link ${isActive ? "active" : ""}`
                }
              >
                <span className="nav-dot" />
                {tab.name}
              </NavLink>
            ))}
          </nav>

          {/* Menu para utilizadors autenticados */}
          {token ? (
            <div className="navActions">
              {/* cone para abrir o menu do utilizador */}
              <IconButton
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleMenuOpen}
                color="inherit"
                className="userButton"
              >
                <AccountCircle />
              </IconButton>

              {/* Menu dropdown com opes de favoritos, conta e logout */}
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                open={Boolean(anchorEl)}
                onClose={handleMenuClose}
              >
                <MenuItem onClick={() => navigate("/favorites")}>
                  Filmes Favoritos
                </MenuItem>
                <MenuItem
                  onClick={() => {
                    navigate("/account");
                    handleMenuClose();
                  }}
                >
                  Definies de Conta
                </MenuItem>
                <MenuItem onClick={handleLogout}>Logout</MenuItem>
              </Menu>
            </div>
          ) : (
            // Botes para login e criao de conta para utilizadors no autenticados
            <div className="authButtons">
              <NavLink to="/login">
                <Button className="authButton ghost" variant="outlined">
                  Login
                </Button>
              </NavLink>
              <NavLink to="/create_account">
                <Button className="authButton">
                  Sign Up
                </Button>
              </NavLink>
            </div>
          )}
        </Toolbar>
      </AppBar>
    </div>
  );
};

export default Header;
