import React, { useEffect, useState, useContext } from "react";
import axios from "axios";
import { CircularProgress } from "@mui/material";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../context/AuthContext"; // <-- Token do seu backend
import { bearerToken } from "../modules/ApiLinks"; // <-- Token da API TMDB
import { getDiscoverMoviesUrl, getDiscoverTvUrl } from "../modules/ApiLinks";
import "../styles/DisplayStyle.css";

// cones de corao (react-icons)
import { AiFillHeart, AiOutlineHeart } from "react-icons/ai";

/** 
 * Representa um item de filme ou srie (Media).
 * Ajuste conforme suas necessidades 
 */
interface Media {
  id: number;
  title?: string;
  poster_path: string | null;
  release_date?: string;
  vote_average: number;
  first_air_date?: string;
  name?: string;
}

/**
 * Se quiser aceitar filtros de "descoberta", crie esta interface.
 * Se voc no usar, basta ignorar ou deixar undefined.
 */
interface Filters {
  genre?: number | null;
  certification?: string | null;
  year?: number | null;
}

/**
 * Props para o componente Display
 */
interface DataProps {
  apiEndPoint: string;      // Endpoint fixo, ex: popularMovies, topRatedMovies etc.
  numberOfMedia: number;    // Quantos itens exibir
  showButtons: boolean;     // Se mostra botes de navegao (pgina)
  tvShowOn: boolean;        // Se  para exibir "name" e "first_air_date"
  moviesOn: boolean;        // Se  para exibir "title" e "release_date"
  itemHeading: string;      // Ttulo para exibir na sesso
  customMedia?: Media[];    // Lista manual de mdia (ex: resultados customizados)
  filters?: Filters;        // Filtros de discover (opcional)
}

const Display: React.FC<DataProps> = ({
  apiEndPoint,
  numberOfMedia,
  showButtons,
  tvShowOn,
  moviesOn,
  itemHeading,
  customMedia,
  filters,
}) => {
  const [showItems, setShowItems] = useState<Media[]>([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [loading, setLoading] = useState(true);
  const [favoriteIds, setFavoriteIds] = useState<string[]>([]);

  const { token } = useContext(AuthContext);
  const navigate = useNavigate();

  /**
   * Efeito principal: se recebermos `customMedia`, usamos direto;
   * caso contrrio, buscamos via endpoint fixo OU discover (se `filters` estiver preenchido).
   */
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);

      try {
        // Se houver customMedia, apenas exibe-a (sem chamada  API).
        if (customMedia) {
          setShowItems(customMedia.slice(0, numberOfMedia));
          setTotalPages(1);
          return;
        }

        let finalEndpoint = apiEndPoint;

        // Se tivermos filtros de discover selecionados (genre, certification ou year),
        // podemos montar uma URL de discover. 
        const hasFilters =
          filters &&
          (filters.genre || filters.certification || filters.year);

        if (hasFilters) {
          if (moviesOn) {
            // Descobrir filmes
            finalEndpoint = getDiscoverMoviesUrl({
              with_genres: filters?.genre ? String(filters.genre) : undefined,
              certification: filters?.certification || undefined,
              primary_release_year: filters?.year || undefined,
            });
          } else if (tvShowOn) {
            // Descobrir sries
            finalEndpoint = getDiscoverTvUrl({
              with_genres: filters?.genre ? String(filters.genre) : undefined,
              certification: filters?.certification || undefined,
              first_air_date_year: filters?.year || undefined,
            });
          }
        }

        const response = await axios.get(finalEndpoint, {
          params: { page: currentPage },
          headers: {
            Authorization: `Bearer ${bearerToken}`,
          },
        });

        const { results, total_pages } = response.data;
        setShowItems(results.slice(0, numberOfMedia));
        setTotalPages(total_pages);
      } catch (error) {
        console.error("Error fetching data:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [
    customMedia,
    currentPage,
    apiEndPoint,
    numberOfMedia,
    filters,
    tvShowOn,
    moviesOn,
  ]);

  /**
   * Busca IDs de favoritos do backend local, se o utilizador estiver logado (token).
   */
  useEffect(() => {
    const fetchFavoriteIds = async () => {
      if (!token) return;
      try {
        const response = await axios.get("http://localhost:8080/favorites", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        setFavoriteIds(response.data.favorites);
      } catch (error) {
        console.error("Error fetching favorite IDs:", error);
      }
    };
    fetchFavoriteIds();
  }, [token]);

  /**
   * Funes de Paginao
   */
  const prevItemsPage = () => {
    if (currentPage > 1) {
      setCurrentPage((prevPage) => prevPage - 1);
    }
  };

  const nextItemsPage = () => {
    if (currentPage < totalPages) {
      setCurrentPage((prevPage) => prevPage + 1);
    }
  };

  /**
   * Lidar com Favoritos (adicionar ou remover)
   */
  const handleFavoriteClick = async (movieId: string) => {
    // Se no estiver logado, apenas avisa no console (ou faa outra ao):
    if (!token) {
      console.log("You need to log in to add favorites!");
      return;
    }

    const isFavorited = favoriteIds.includes(movieId);

    try {
      if (isFavorited) {
        await axios.delete(`http://localhost:8080/favorites/${movieId}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        setFavoriteIds((prev) => prev.filter((id) => id !== movieId));
        console.log("Favorite removed!");
      } else {
        await axios.post(
          "http://localhost:8080/favorites",
          { movieId },
          {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${token}`,
            },
          }
        );
        setFavoriteIds((prev) => [...prev, movieId]);
        console.log("Added to favorites!");
      }
    } catch (error) {
      console.error("Error updating favorites:", error);
    }
  };

  /**
   * Formatao de data (dia, ms e ano)
   */
  const getFormattedDate = (dateString: string | number | Date) => {
    const options = {
      year: "numeric",
      month: "short",
      day: "numeric",
    } as Intl.DateTimeFormatOptions;
    const date = new Date(dateString);
    return date.toLocaleDateString("en-US", options);
  };

  const formattedItems = showItems.map((item) => ({
    ...item,
    rating: Math.round(item.vote_average * 10) / 10,
  }));

  return (
    <div className="MovieShowWrapper">
      {loading && (
        <div className="loadingOverlay">
          <CircularProgress size={64} style={{ color: "#ffffff" }} />
          <p>A carregar</p>
        </div>
      )}

      <div className="mediaHeading">
        <h1>{itemHeading}</h1>
      </div>

      {formattedItems.length === 0 && !loading ? (
        <div className="emptyState">Sem resultados para mostrar.</div>
      ) : (
        <div className="mediaCard">
          {formattedItems.map((item) => {
            const isFavorited = favoriteIds.includes(item.id.toString());

            return (
              <div className="media" key={item.id}>
                <div
                  className="mediaImage"
                  onClick={() => navigate(`/movie/${item.id}`)}
                >
                  <img
                    src={
                      item.poster_path
                        ? `https://image.tmdb.org/t/p/w300/${item.poster_path}`
                        : "https://dummyimage.com/400x600/0b1024/ffffff&text=No+Image"
                    }
                    alt={item.title || item.name}
                  />
                  <span>{item.rating}</span>

                  {/* cone de corao no canto superior direito */}
                  <button
                    onClick={(e) => {
                      e.stopPropagation();
                      handleFavoriteClick(item.id.toString());
                    }}
                  >
                    {isFavorited ? (
                      <AiFillHeart color="#FF5555" />
                    ) : (
                      <AiOutlineHeart color="#DDDDDD" />
                    )}
                  </button>
                </div>

                <div className="mediaInfo">
                  {moviesOn && (
                    <>
                      <h4>{item.title}</h4>
                      <p>
                        {item.release_date
                          ? getFormattedDate(item.release_date)
                          : "N/A"}
                      </p>
                    </>
                  )}
                  {tvShowOn && (
                    <>
                      <h4>{item.name}</h4>
                      <p>
                        {item.first_air_date
                          ? getFormattedDate(item.first_air_date)
                          : "N/A"}
                      </p>
                    </>
                  )}
                </div>
              </div>
            );
          })}
        </div>
      )}

      {showButtons && !customMedia && formattedItems.length > 0 && (
        <div className="buttons">
          {currentPage > 1 && (
            <button className="btnPrev" onClick={prevItemsPage}>
              Back
            </button>
          )}
          <p>
            Page <b>{currentPage}</b>
          </p>
          {currentPage < totalPages && (
            <button className="btnNext" onClick={nextItemsPage}>
              Next
            </button>
          )}
        </div>
      )}
    </div>
  );
};

export default Display;
