import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

// Importa as funes do servio de favoritos
import {
  getFavoriteIds,
  getFavoritesDetails,
  Media,
} from "../modules/FavoritesService";

function Favorites() {
  // Estado para armazenar os IDs dos favoritos
  const [favoriteIds, setFavoriteIds] = useState<string[]>([]);

  // Estado para armazenar os detalhes dos filmes favoritos
  const [favorites, setFavorites] = useState<Media[]>([]);

  // Estado para indicar se os dados esto sendo carregados
  const [loading, setLoading] = useState(true);

  // Hook para navegao entre pginas
  const navigate = useNavigate();

  // 1 Efeito: buscar IDs dos filmes favoritos no backend
  useEffect(() => {
    async function fetchFavorites() {
      try {
        // Chama o servio para obter os IDs dos favoritos
        const ids = await getFavoriteIds();
        setFavoriteIds(ids); // Atualiza o estado com os IDs obtidos
      } catch (error) {
        console.error("Error fetching favorites:", error);
      }
    }
    fetchFavorites(); // Executa a funo ao montar o componente
  }, []);

  // 2 Efeito: buscar detalhes dos filmes favoritos utilizando os IDs
  useEffect(() => {
    async function fetchFavoriteDetails() {
      if (favoriteIds.length === 0) {
        setLoading(false); // Atualiza o estado de carregamento se no houver favoritos
        return;
      }

      try {
        setLoading(true); // Define o estado como carregando
        // Chama o servio para obter os detalhes dos filmes favoritos
        const movieData = await getFavoritesDetails(favoriteIds);
        setFavorites(movieData); // Atualiza o estado com os detalhes dos filmes
      } catch (error) {
        console.error("Error fetching favorite details:", error);
      } finally {
        setLoading(false); // Define o estado como carregado
      }
    }

    fetchFavoriteDetails(); // Executa a funo sempre que `favoriteIds` mudar
  }, [favoriteIds]);

  // Exibe uma mensagem enquanto os dados esto sendo carregados
  if (loading) {
    return <div>Loading favorites...</div>;
  }

  // Exibe uma mensagem caso no haja filmes favoritos
  if (favoriteIds.length === 0) {
    return <div>You have no favorite movies yet.</div>;
  }

  return (
    <div className="favorites-wrapper">
      <h2>Your Favorite Movies</h2>
      <div className="mediaCard favorites-grid">
        {/* Itera sobre os favoritos e renderiza os detalhes de cada filme */}
        {favorites.map((item) => (
          <div
            className="media"
            key={item.id}
            onClick={() => navigate(`/movie/${item.id}`)} // Navega para a pgina do filme ao clicar
          >
            <div className="mediaImage">
              <img
                src={
                  item.poster_path
                    ? `https://image.tmdb.org/t/p/w300/${item.poster_path}` // URL da imagem do filme
                    : "https://dummyimage.com/400x600/0b1024/ffffff&text=No+Image" // Placeholder caso no haja imagem
                }
                alt={item.title}
              />
              <span>{Math.round(item.vote_average * 10) / 10}</span> {/* Exibe a nota mdia */}
            </div>
            <div className="mediaInfo">
              <h4>{item.title}</h4> {/* Exibe o ttulo do filme */}
              <p>{item.release_date}</p> {/* Exibe a data de lanamento */}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Favorites;
