import React, { useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../context/AuthContext";

// 1. Importa o servio de login e a interface para os dados do formulrio.
import { doLogin } from "../modules/LoginService";
import type { LoginFormData } from "../modules/LoginService";

// 2. Importa o CSS para estilizao.
import "../styles/LoginStyle.css";

const Login: React.FC = () => {
  // Estado local para armazenar os dados do formulrio de login.
  const [formData, setFormData] = useState<LoginFormData>({
    email: "",
    password: "",
  });

  // Estado para exibir mensagens de erro caso algo d errado.
  const [error, setError] = useState<string | null>(null);

  // Obtm o contexto de autenticao e a funo para redirecionamento.
  const { setToken } = useContext(AuthContext);
  const navigate = useNavigate();

  // Atualiza o estado do formulrio conforme o utilizador preenche os campos.
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  // Lida com o envio do formulrio e chama o servio de login.
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault(); // Previne o comportamento padro de recarregar a pgina.
    setError(null); // Reseta o erro antes de tentar o login.

    try {
      // Chama a funo `doLogin` do servio para autenticar o utilizador.
      const token = await doLogin(formData);

      // Armazena o token no localStorage para persistncia.
      localStorage.setItem("token", token);

      // Define o token no contexto para manter o utilizador autenticado.
      setToken(token);

      // Redireciona para a pgina inicial aps login bem-sucedido.
      navigate("/");
    } catch (err: any) {
      // Define a mensagem de erro retornada pelo servio ou uma mensagem genrica.
      setError(err.response?.data?.message || "Erro ao fazer login.");
    }
  };

  return (
    <div className="login-container">
      <h1>Login</h1>
      <p className="login-subtitle">Retoma a tua watchlist com uma entrada rpida.</p>

      {/* Formulrio de login */}
      <form onSubmit={handleSubmit} className="login-form">
        {/* Campo para o email */}
        <input
          type="email"
          name="email"
          placeholder="Email"
          value={formData.email}
          onChange={handleChange}
          className="login-input"
          required
        />

        {/* Campo para a senha */}
        <input
          type="password"
          name="password"
          placeholder="Password"
          value={formData.password}
          onChange={handleChange}
          className="login-input"
          required
        />

        {/* Boto para submeter o formulrio */}
        <button type="submit" className="login-button">
          Submit
        </button>
      </form>

      {/* Exibe uma mensagem de erro, se houver */}
      {error && <p className="error-text">{error}</p>}
    </div>
  );
};

export default Login;
