import React, { useState, useCallback } from "react";
import CoverPage from "../components/CoverPage";
import FilterBar from "../components/FilterBar";
import Section from "../components/Section";
import coverImage from "../assets/img2.jpg";

// Importa os endpoints predefinidos da API.
import {
  popularMovies,
  topRatedMovies,
  upcomingMovies,
} from "../modules/ApiLinks";

// Interface para definir o formato dos filtros, evitando o uso de 'any'.
interface Filters {
  genre: number | null;           // ID do gnero selecionado.
  certification: string | null;   // Certificao selecionada (ex.: PG, R).
  year: number | null;            // Ano selecionado para filtrar os filmes.
}

// Componente funcional para a pgina inicial.
const Home: React.FC = () => {
  // Estado para armazenar os filtros selecionados pelo utilizador.
  const [filters, setFilters] = useState<Filters>({
    genre: null,
    certification: null,
    year: null,
  });

  // Funo para atualizar os filtros, utilizando `useCallback` para otimizao.
  const handleFilterChange = useCallback((newFilters: Filters) => {
    setFilters(newFilters); // Atualiza o estado com os novos filtros.
  }, []);

  return (
    <div>
      {/* Componente de pgina de capa */}
      <CoverPage
        title="Welcome to Movies" // Ttulo principal da pgina.
        description="The best Website to stay updated on movies!" // Descrio do site.
        catchyPhrase="Check out new releases, popular titles, and much more!" // Frase de destaque.
        headerImage={coverImage} // Imagem de capa.
        showSearch={true} // Exibe a barra de pesquisa.
        showHeaderImage={true} // Exibe a imagem de cabealho.
      />

      {/* Barra de Filtros */}
      <FilterBar onFilterChange={handleFilterChange} />

      {/* Seo de filmes populares */}
      <Section
        heading="Popular" // Ttulo da seo.
        apiEndpoint={popularMovies} // Endpoint da API para filmes populares.
        moviesOn={true} // Define que a seo exibe filmes.
        tvShowOn={false} // Define que a seo no exibe sries.
        numberOfMedia={10} // Nmero de mdias exibidas.
        filters={filters} // Aplica os filtros selecionados pelo utilizador.
      />

      {/* Seo de filmes mais bem avaliados */}
      <Section
        heading="Top Rated"
        apiEndpoint={topRatedMovies}
        moviesOn={true}
        tvShowOn={false}
        numberOfMedia={10}
        filters={filters}
      />

      {/* Seo de lanamentos futuros */}
      <Section
        heading="Upcoming Releases"
        apiEndpoint={upcomingMovies}
        moviesOn={true}
        tvShowOn={false}
        numberOfMedia={10}
        filters={filters}
      />
    </div>
  );
};

export default Home;
