import React from "react";
import Display from "../components/Display"; // Importa o componente Display, responsvel por exibir mdias.
import { popularMovies } from "../modules/ApiLinks"; // Importa o endpoint da API para filmes populares.

const Popular = () => {
  return (
    <div>
      {/* Componente Display que exibe os filmes populares */}
      <Display
        apiEndPoint={popularMovies} // URL da API para obter os filmes populares.
        itemHeading="Popular Movies" // Ttulo exibido no componente.
        showButtons={true} // Indica se os botes (ex.: prximo/anterior) devem ser exibidos.
        moviesOn={true} // Define que a mdia exibida ser de filmes.
        tvShowOn={false} // Define que sries de TV no sero exibidas.
        numberOfMedia={10} // Nmero mximo de mdias a serem exibidas.
      />
    </div>
  );
};

export default Popular;
