// Section.tsx
import React from "react";
import Display from "./Display"; // Importa o componente Display para exibir os itens.
import "../styles/FilterBarStyle.css"; // Importa o CSS para estilizao.

// Define as propriedades aceitas pelo componente Section.
interface SectionProps {
    heading: string; // Cabealho da seo (ttulo).
    apiEndpoint?: string; // Endpoint da API (opcional).
    moviesOn?: boolean; // Define se a seo exibe filmes (opcional, padro: true).
    tvShowOn?: boolean; // Define se a seo exibe sries de TV (opcional, padro: false).
    numberOfMedia?: number; // Nmero de itens a serem exibidos (opcional, padro: 10).
    filters?: {
        genre: number | null; // Filtro de gnero (opcional).
        certification: string | null; // Filtro de certificao (opcional).
        year: number | null; // Filtro por ano (opcional).
    };
}

// Componente funcional Section que renderiza uma seo configurvel de mdia.
const Section: React.FC<SectionProps> = ({
    heading, // Ttulo da seo.
    apiEndpoint, // Endpoint da API para buscar os itens.
    moviesOn = true, // Exibe filmes por padro.
    tvShowOn = false, // No exibe sries de TV por padro.
    numberOfMedia = 10, // Exibe at 10 itens por padro.
    filters, // Filtros personalizados.
}) => {
    return (
        <div className="section-container">
            {/* Componente Display responsvel por renderizar os itens */}
            <Display
                apiEndPoint={apiEndpoint ?? ""} // Usa o endpoint ou string vazia se indefinido.
                itemHeading={heading} // Define o ttulo da seo.
                showButtons={true} // Exibe botes de navegao.
                moviesOn={moviesOn} // Configura a exibio de filmes.
                tvShowOn={tvShowOn} // Configura a exibio de sries de TV.
                numberOfMedia={numberOfMedia} // Define o nmero de itens exibidos.
                filters={filters} // Aplica os filtros personalizados.
            />
        </div>
    ); 
};

export default Section;
