// context/AuthContext.tsx
import { jwtDecode } from "jwt-decode"; // Importa a funo para decodificar o token JWT.
import React, { createContext, useState, useEffect } from "react";

// Interface para definir o formato do contexto de autenticao.
interface AuthContextType {
  token: string | null; // Token JWT armazenado.
  setToken: (token: string | null) => void; // Funo para atualizar o token.
  user: { username: string; email: string } | null; // Informaes do utilizador autenticado.
  logout: () => void; // Funo para realizar o logout.
}

// Cria o contexto de autenticao com valores padro.
export const AuthContext = createContext<AuthContextType>({
  token: null,
  setToken: () => { }, // Funo vazia padro.
  user: null,
  logout: () => { }, // Funo vazia padro.
});

// Componente AuthProvider para gerenciar o estado de autenticao.
export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  // Estado para armazenar o token JWT, inicializado com o valor armazenado no localStorage.
  const [token, setToken] = useState<string | null>(localStorage.getItem("token"));

  // Estado para armazenar as informaes do utilizador decodificadas do token.
  const [user, setUser] = useState<{ username: string; email: string } | null>(null);

  // Efeito para decodificar o token JWT e extrair as informaes do utilizador.
  useEffect(() => {
    if (token) {
      try {
        const decoded: any = jwtDecode(token); // Decodifica o token.
        setUser({
          username: decoded.username, // Extrai o nome de utilizador do token.
          email: decoded.email, // Extrai o email do token.
        });
      } catch (err) {
        console.error("Token invlido."); // Exibe um erro se o token no puder ser decodificado.
        setUser(null); // Reseta as informaes do utilizador.
        setToken(null); // Reseta o token.
        localStorage.removeItem("token"); // Remove o token invlido do localStorage.
      }
    } else {
      setUser(null); // Reseta o utilizador caso no haja token.
    }
  }, [token]); // Executa sempre que o token mudar.

  // Funo para realizar o logout do utilizador.
  const logout = () => {
    setToken(null); // Reseta o token no estado.
    setUser(null); // Reseta as informaes do utilizador.
    localStorage.removeItem("token"); // Remove o token do localStorage.
  };

  // Retorna o provedor do contexto de autenticao com os valores necessrios.
  return (
    <AuthContext.Provider value={{ token, setToken, user, logout }}>
      {children} {/* Renderiza os filhos do AuthProvider */}
    </AuthContext.Provider>
  );
};
